$font-weights: (
    'regular': 400,
    'medium': 500,
    'bold': 700
);
body{
    height: 50vh;
    margin: 0;
    padding: 0;
}
form{
    display: flex;
    flex-direction: column;
    align-items: center;
    max-width: 400px;
    width: 60%;
    height: 20%;
    padding: 20px;
    border: 1px solid #0000005e;
    background-image: linear-gradient(to right,rgba(255, 145, 0, 0.596), rgba(255, 255, 0, 0.719));
    border-radius: 20px;
    gap: 20px;
    margin-top: 20px;
}
.home_container{
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    height: 45vh;
}
.logo1{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-size: cover;
    background-position: center;
    z-index: -1;
    box-shadow: 0px 1px 12px 0px rgba(0,0,0,0.45);
}
.logo2{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 160%;
    background-size: cover;
    background-position: center;
    z-index: -1;
    box-shadow: 0px 1px 12px 0px rgba(0,0,0,0.45);
}
.home_header{
    display: flex;
    align-items: center;
    justify-content: center;
    position: absolute;
    top: 0;
    background-image: linear-gradient(rgba(255, 0, 0, 0.24), yellow);
    height: 15%;
    width: 100%;
    font-size: 12px;
    font-family:Georgia, 'Times New Roman', Times, serif;
    box-shadow: 0px 1px 12px 0px rgba(0,0,0,0.45);
}
.soupe_home_container {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    margin: 10px;
  }
  .soup_img_container {
    flex: 0 0 calc(33.33% - 20px); /* Adjust the width based on the number of images per row */
    margin: 10px;
    box-sizing: border-box;
    text-align: center;
  }
  .soupe_img {
    max-width: 100%;
    height: auto;
    box-shadow: 0px 5px 15px 0px rgba(0,0,0,0.62);
    border-radius: 12px;
  }
  .soup_text {
    margin-top: 10px;
  }
  .button1{
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 0 20px 20px 0px;
    background-image: linear-gradient(rgba(255, 174, 0, 0.39), rgba(255, 255, 0, 0.726));
    height: 15%;
    cursor: pointer;
    overflow: hidden;
  }
  .search1_logo{
    background-image: linear-gradient(rgba(255, 174, 0, 0.082), rgba(255, 255, 0, 0.411));
    border-radius: 0 20px 20px 0px;
    width: 100%;
    height: 100%;
  }
  .select_container{
    display: flex;
    justify-content: center;
    margin-top: -10px;
  }
  #soup {
    width: 50%;
    height: 15%;
  }
  .h1_text1{
    font-size: 17px;
    margin-top: -10px;
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
  }
.logo_restaurant{
  width: 6%;
  position: absolute;
  left: 3%;
}
  //food.js
.food_container{
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin: 0 20px;
}
.soup_title{
  text-align: center;
  font-family:Georgia, 'Times New Roman', Times, serif;
}
.food_text{
  text-align: justify;
  word-spacing: 2px;
  font-weight: 700;
  color: rgb(0, 0, 0);
  background-color: rgba(237, 245, 192, 0.726);
  font-size: 20px;
  padding: 20px;
  border-radius: 6px;
  box-shadow: 0px 5px 15px 0px rgba(0, 0, 0, 0.507);
  inline-size: 90%;
}
.soup_img_main{
  margin: 20px;
  width: 300px;
  box-shadow: 0px 5px 15px 0px rgba(0,0,0,0.62);
}
.button_return {
  margin-top: 20px;
  background-color: #b1b0b05b;
  border: none;
  color: white;
  border-radius: 60px;
  padding: 10px 20px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  cursor: pointer;
  box-shadow: 0px 5px 15px 0px rgba(0,0,0,0.62);
}
.img_return {
  width: 20px;
  height: 20px;
  margin-right: 5px;
}
.loading{
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 20px;
}
  @media screen and (max-width: 768px) {
    .soup_img_container {
      flex: 0 0 calc(50% - 20px); /* Two images per row for smaller screens */
    }
  }
  @media screen and (min-height: 800px) {
    .h1_text1{
    margin: 0;
    }
    .select_container{
      margin: 0;
    }
    
  }
  /* Media query for medium-sized screens */
  @media screen and (min-width: 769px) and (max-width: 1400px) {
    .soup_img_container {
      flex: 0 0 calc(33.33% - 20px); /* Three images per row for screens between 769px and 1400px */
    }
    .logo_restaurant{
    width: 4%;
    }
    .food_text{
      inline-size: 70%;
    }
    
  }
  /* Media query for larger screens */
  @media screen and (min-width: 1401px) {
    .soup_img_container {
      flex: 0 0 calc(25% - 20px); /* Four images per row for screens larger than 1400px */
    }
    .soupe_home_container{
        margin-top: 30px;
    }
    option{
      font-size: 14px;
    }
    .logo_restaurant{
      width: 3%;
      }
      .food_text{
        inline-size: 50%;
      }
  }
  @media screen and (min-width: 1900px) {
    .soup_img_container {
      flex: 0 0 calc(20% - 20px);
    }
    .soupe_home_container{
        margin-top: 40px;
    }
    .home_header{
      font-size: 16px;
    }
    .h1_text1{
      font-size: 18px;
    }
    option{
      font-size: 16px;
    }
    .logo_restaurant{
      width: 2.5%;
      }
      .food_text{
        inline-size: 35%;
      }
      .soup_img_main{
      width: 400px;
      }
      .soup_title{
      font-size: 40px;
      }
  }
 
  @media screen and (min-width: 2300px) {
    .soup_img_container {
      flex: 0 0 calc(15% - 20px);
    }
    .logo_restaurant{
      width: 1.5%;
      }
  }